import React from 'react'
import { graphql, useStaticQuery, Link } from "gatsby";
import { Container, Row, Col } from 'react-bootstrap'
import { StaticImage } from "gatsby-plugin-image";
import SingleBlogItem from '../../../components/single-blog-item'
import Button from '../../../components/shared/button';
import ArchivePostArea from '../archive-post';
import {
    SectionWrap,
    SectionTitle,
    Title,
    ButtonBox,
    ArchivePostInnerWrap,
    AddBannerArea
} from './style'
import FollowUsWidget from '../follow-us';


const RecentArticleArea = () => {
    const recentArticlesAreaQuery = useStaticQuery(graphql`
    query RecentArticlesTwoQuery {
        allAggVideo(
          limit: 14
          sort: {order: DESC, fields: publishTime}
          filter: {channelId: {ne: "UCT_r9y3ECIg0tHAFDvK_V8w"}}
        ) {
          edges {
            node {
              id
              excerpt: description
              title
              publishedAt(formatString: "DD MMMM YYYY")
              is_featured: key
              thumbnails {
                default {
                    url
                }
                medium {
                  url
                }
              }
              channelId
            }
          }
        }
      }
    `);

    const recentArticleData = recentArticlesAreaQuery.allAggVideo.edges;
    return (
        <SectionWrap>
            <Container>
                <Row>
                    <Col lg={8}>
                        <SectionTitle>
                            <Title>Recent Videos</Title>
                        </SectionTitle>

                        <Row className="row--30">
                            {recentArticleData && recentArticleData.slice(0, 4).map((latestPosts, i) => {
                                return ( 
                                    <Col lg={6} md={6} key={i}>
                                        <SingleBlogItem
                                            title={latestPosts.node.title}
                                            thume_image={latestPosts.node.thumbnails.medium.url}
                                            date={latestPosts.node.publishedAt}
                                            slug={`listing/${latestPosts.node.id}`}
                                            body={latestPosts.node.excerpt}
                                            authorId={latestPosts.node.title}
                                            dateSlug={latestPosts.node.title}
                                        />
                                    </Col>
                                );
                            })}

                        </Row>
                    </Col>
                    <Col lg={4}>
                        <ArchivePostInnerWrap>
                                
                            <ArchivePostArea archivePosts={recentArticleData && recentArticleData.slice(4)} />

                            <FollowUsWidget/>
                            
                            {
                                "" && <AddBannerArea>
                                <Link to="/">
                                    <StaticImage src="../../../data/images/archive-post/add-banner-01.jpg" alt=""/>
                                </Link>
                                </AddBannerArea>
                            }

                        </ArchivePostInnerWrap> 

                    </Col>
                </Row>
            </Container>
        </SectionWrap>
    )
}

export default RecentArticleArea
