import React, { useEffect, useState } from 'react'
import {graphql, useStaticQuery } from "gatsby";
import SideLatestPosts from '../../../components/side-latest-post';
import {
    ArchivePostInnerWrap,
    ArchivePostHeader,
    ArchivePostBox
} from "./style";

const ArchivePostArea = ({  archivePosts }) => {
  console.log(archivePosts)
    const [posts, setPosts] = useState([])
    const archivePostsQuery = useStaticQuery(graphql`
    query RecentArticlesTwoQuery3333 {
        allAggVideo(limit: 8) {
          edges {
            node {
              id
              excerpt: description
              title
              date: publishedAt(formatString: "DD MMMM YYYY")
              is_featured: key
              thumbnails {
                default {
                  url
                }
              }
      
            }
          }
        }
      }
    `);
    const archivePostsData = archivePosts?.allAggVideo?.edges || [];
    useEffect(() => {
      setPosts(archivePosts)
    }, [archivePosts])
    return (
        <ArchivePostInnerWrap>
            <ArchivePostHeader>
                <div className="section-title">
                    <h3>More Videos</h3>
                </div>
            </ArchivePostHeader>
            <ArchivePostBox>
                {posts.map((latestPosts, i) => {
                    return ( 
                        <SideLatestPosts
                            key={i}
                            title={latestPosts.node.title}
                            thume_image={latestPosts.node.thumbnails.default.url}
                            date={latestPosts.node.date}
                            dateSlug={latestPosts.node.title}
                            slug={`listing/${latestPosts.node.id}`}
                        />
                    );
                })}
            </ArchivePostBox>
        </ArchivePostInnerWrap>
    )
}


export default ArchivePostArea
