import React from 'react'
import {Col, Container, Row} from 'react-bootstrap'
import { graphql, useStaticQuery } from 'gatsby'
import SpecialBannerTwo from '../../../components/special-for-beginner-two'
import {
    SpecialForBeginnerArea,
    SectionTitle,
    Title
} from './style'

const SpecialForBeginner = () => {

    const specialForBeginnerQuery = useStaticQuery(graphql`
    query RecentArticlesTwoQuery44444 {
        allAggVideo {
          group(field: channelId, limit: 1) {
            edges {
              node {
                id
                channelTitle
                thumbnails {
                  medium {
                    url
                  }
                  high {
                    url
                  }
                }
              }
            }
          }
        }
      }
    `)
    const specialBeginnerTwoData = specialForBeginnerQuery.allAggVideo.group

    return (
        <SpecialForBeginnerArea>
            <Container>
                <Row>
                    <Col lg={12}>
                        <SectionTitle>
                            <Title>Channels</Title>
                        </SectionTitle>
                    </Col>
                </Row>
                <Row className="row--35">
                    {specialBeginnerTwoData && specialBeginnerTwoData.map((itemGroup, i) => {
                        console.log(itemGroup)
                        const item = itemGroup.edges[0]
                        return (
                            <Col lg={4} md={6} sm={6} key ={i}> 
                                <SpecialBannerTwo
                                    title={item.node.channelTitle}
                                    thume_image={item.node.thumbnails.medium.url}
                                    date={item.node.date}
                                    slug={`listing/${item.node.id}`}
                                    authorSlug={""}
                                    dateSlug={""}
                                />
                            </Col>
                        );
                    })}
                </Row>
            </Container>
        </SpecialForBeginnerArea>
    )
}

export default SpecialForBeginner
